import "./App.css";
import { useState } from "react";
import CreateAccountForm from "./CreateAccountForm";
import { generateFakeData } from "./fakeData";
import IsUserOffline from "./isUserOffline";
import { clearData, createBulk, getData, table } from "./database";
import AccountInfo from "./AccountInfo";
import { dropRecord, getNewAccount, listen } from "./socket";

function Difference() {
  const [form, setForm] = useState(false);
  const [createAt, setCreateAt] = useState("0");
  const [displayAt, setDisplayAt] = useState("0");

  const [createAtIndexDb, setCreateAtIndexDB] = useState("0");
  const [displayAtIndexDb, setDisplayAtIndexDB] = useState("0");

  const [numberOfData, setNumberOfData] = useState(0);

  const [dbData, setDbData] = useState(null);
  const [account, newAccount] = useState([]);
  const [isLoading, setIsLoading] = useState(false);

  const generateStyle = {
    maxWidth: "500px",
    margin: "0 auto",
  };

  const generateData = () => {
    setIsLoading(true);
    const size = 100 * 100;
    const data = generateFakeData(size);
    setNumberOfData(size);
    createBulk(
      data,
      (time, data) => {
        setCreateAt(time);
        setIsLoading(false);
      },
      true
    );
    createBulk(data, (time, data) => {
      setCreateAtIndexDB(time);
      setIsLoading(false);
    });
  };

  const viewData = () => {
    setIsLoading(true);
    getData((time, data) => {
      setDisplayAt(time);
      setIsLoading(false);
    });
    getData((time, data) => {
      setDisplayAtIndexDB(time);
    }, true);
  };

  const clearDatabase = () => {
    clearData();
  };
  return (
    <div className="App">
      <IsUserOffline />
      <header className="title App-header">
        <h1>Difference Between Local and IndexDB Storage</h1>
      </header>
      {isLoading && <div className="loader"></div>}
      <h2> LocalStorage accounts </h2>

      <div className="timestamp">
        <div>
          <span> Created In </span>
          {Math.abs(createAt).toFixed(3)}
        </div>
        <div className="display">
          <span> Displayed withIn </span>
          {Math.abs(displayAt).toFixed(3)}
        </div>
      </div>
      <h2> IndexDb accounts </h2>

      <div className="timestamp">
        <div className="created">
          <span> Created In </span>
          {Math.abs(createAtIndexDb).toFixed(3)}
        </div>
        <div>
          <span> Displayed withIn </span>
          {Math.abs(displayAtIndexDb).toFixed(3)}
        </div>
      </div>

      <div style={generateStyle} className="overflow">
        <div>
          Generated Records: <span>{numberOfData}</span>
        </div>
        <br />
        <div
          style={{
            display: "flex",
            width: "80%",
            justifyContent: "space-around",
          }}
        >
          <button onClick={generateData} style={{ margin: "10px" }}>
            generate
          </button>
          <button
            onClick={viewData}
            style={{ margin: "10px", background: "#123" }}
          >
            display records
          </button>
          <button
            onClick={clearDatabase}
            style={{ margin: "10px", background: "#db3434" }}
          >
            clear records
          </button>
        </div>
      <br />
      <br />
          <h4> OTHER URL </h4>
          <a href="/local"> view LocalStorage App data</a>
      <br />

          <a href="/indexDB"> view IndexDB App data</a>
      </div>
      <h4> Time Difference between IndexDB and LocalStorage </h4>

      <div className="timestamp">
        <div className="created">
          <span> Created In </span>
          {Math.abs(createAtIndexDb - createAt).toFixed(3)}
        </div>
        <div>
          <span> Displayed withIn </span>
          {Math.abs(displayAtIndexDb - displayAt).toFixed(3)}
        </div>
      </div>
    </div>
  );
}

export default Difference;
